import React from "react";

import "./CarouselComponent.scss"

export class Carousel extends React.Component{
    constructor(){
        super();
        this.state = {
            bannerDetails: [{
                "bannerName":"banner2.jpg"
            }, 
            {
                "bannerName":"banner3.jpg"
            }],
            description: "EARTHA BABA",
            currentBanner: 0,
            interval: null
        }
    }

    componentDidMount() {
        this.setState({
            interval:setInterval( () =>{
                this.carouselScroll();
            }, 5000)
        })
    }

    componentWillUnmount() {
        window.clearInterval(this.state.interval);
        this.setState({interval: null});
    }

    render(){
        const banners = this.state.bannerDetails.map((banner,index)=>{
            return (
                <div key={index} style={{backgroundImage:`url(${process.env.PUBLIC_URL+"/assets/img/"+banner.bannerName})`}} className="banner" />
            )
        });
        const circle = this.state.bannerDetails.map((banner, index) => {
            return (
                <div value={index} key={index} className={`circle ${index === this.state.currentBanner ? "active" : ""}`} onClick={() => this.carouselScroll(index)}></div>
            )
        });
        return(
         <div className="carousel">
                <div className="carousel__container">
                    {banners}
                </div>
                <div className="carousel__controls">
                    {circle}
                </div>
            </div>
            
        )
    }

    carouselScroll(index = null) {
        const carousel = document.querySelector('.carousel__container');
        const width = carousel.clientWidth;
        if(index != null) {
            if(index === this.state.currentBanner){
                return;
            }
            this.setState({currentBanner: index});
            carousel.scrollTo(index * width, 0);
            clearInterval(this.state.interval);
            this.setState({
                interval:setInterval( () =>{
                    this.carouselScroll();
                }, 5000)
            })
            return;
        }
        if(this.state.currentBanner === this.state.bannerDetails.length -1) {
            this.setState({currentBanner: 0 });
            carousel.scrollTo(0, 0);
            return;
        }
        this.setState({currentBanner: this.state.currentBanner+1 });
        carousel.scrollTo(this.state.currentBanner * width, 0);
        return;
    }

    
}

export default Carousel;